
import Popup from 'vux/src/components/popup/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'

import api from '@/utils/api'
import { getTimeRange } from '@/utils/utils'

export default {
name: 'Activity',
components: {
Popup
},
data () {
return {
activityId: '',
activity: {},
visible: false,
username: '',
phone: '',
status: false 
}
},
computed: {
getAppointTime () {
const { starttime, endtime } = this.activity
return getTimeRange(starttime, endtime)
},
isDisabled () {
return !(+this.activity.status === 1) || this.status
}
},
methods: {
getActivityId () {
this.activityId = querystring.parse().activityId
},
showPopup () {
if (this.isDisabled) {
return
}
this.visible = true
},
hidePopup () {
this.visible = false
},
async fetchData (id) {
try {
const data = await api.getActivityDetail(this.activityId)
if (data) {
this.activity = data
document.title = this.activity.title
}
} catch (err) {
console.error(err)
}
},
async appoint () {
try {
const data = await api.appoitActivity({
id: this.activityId,
name: this.username,
phone: this.phone
})

if (data.code === 0) {
return this.$vux.toast.text('预约失败，请重新预约')
}

if (data.code === 1 && data.data && data.data.response.id) {
this.status = true
this.$vux.toast.text('预约成功')
this.saveLocalInfo()
} else {
this.$vux.toast.text('您已经预约过')
}
this.hidePopup()
} catch (err) {
console.error(err)
}
},
handleSubmit () {
const isValid = this.checkFormData({
username: this.username,
phone: this.phone
})
if (!isValid) {
return
}
this.appoint()
},
saveLocalInfo () {
localStorage.setItem(`activity_${this.activityId}`, JSON.stringify({
username: this.username,
phone: this.phone
}))
},
getLocalInfo () {
const data = localStorage.getItem(`activity_${this.activityId}`)
if (data) {
const { username, phone } = JSON.parse(data)
this.username = username
this.phone = phone
}
},
checkFormData ({ username = '', phone = '' }) {
if (!username) {
this.$vux.toast.text('姓名不能为空哦')
return false
}

if (!phone) {
this.$vux.toast.text('联系方式不能为空哦')
return false
}

if (!/\d{11}/.test(phone)) {
this.$vux.toast.text('请填入合法的手机号哦')
return false
}

return true
}
},
created () {
this.getActivityId()
this.fetchData()
},
mounted () {
this.getLocalInfo()
}
}



